.profile-detail-wrapper {
  background-color: #e5e5e5;
  padding: 20px;
}
.profile-detail-wrapper .profile-detail {
  background-color: #fff;
  padding: 15px;
  border: 1px solid #e1e1e1;
  box-shadow: 1px 2px 14px 0 #e1e1e1;
  border-radius: 5px;
}
.profile-detail-wrapper .profile-detail .profile-inner {
  border: 1px solid #e5e5e5;
  border-radius: 5px;
}
.profile-detail-wrapper .profile-detail .profile-inner .spouse {
  display: inline-block;
  margin-right: 5px;
}
.profile-detail-wrapper .profile-detail .profile-inner .spouse::after {
  content: ",";
  position: relative;
  right: 0;
  top: 0;
}
.profile-detail-wrapper
  .profile-detail
  .profile-inner
  .spouse:last-child::after {
  content: "";
}
.profile-bar {
  background-color: #17a2b8;
}
.image-block-container {
  margin: 10px;
}

.image-block-container .image-block {
  display: block;
  background-size: cover;
  height: 300px;
  width: 225px;
  border: 2px solid #cacaca;
}
