.s-card {
  cursor: pointer;
  height: 133px;
  width: 100px;
  border: 1px solid #d0e8ef;
  margin: auto;
  padding: 5px;
  background-color: #ecf6fe;
}
.s-card .thub-holder {
  display: block;
}
.s-card .place-holder {
  display: block;
  height: 105px;
  border: 1px solid #ccc;
  box-sizing: content-box;
  background-color: #ecf6fe;
  color: #000;
}
.s-card .place-holder .thumb {
  font-size: 80px;
  padding-top: 12px;
}

.s-card .thub-holder .img-block {
  background-color: #ecf6fe;
  height: 105px;
  display: block;
  border: 1px solid #ccc;
  background-size: cover;
}
.s-card .name {
  display: block;
  font-size: 12px;
  line-height: 16px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
  color: #000;
}
.s-card.isNotAlive {
  background-color: #dc3545;
  color: #fff;
}
.s-card.isNotAlive .name {
  color: #fff;
}

@media (max-width: 530px) {
  .s-card {
    height: 100px;
    width: 75px;
  }
  .s-card .place-holder {
    height: 65px;
  }
  .s-card .place-holder .thumb {
    font-size: 40px;
  }
  .s-card .thub-holder .img-block {
    height: 75px;
  }
}
